//COLORS



:root {
  --color-primary: #00043C;
  --color-primary-light: #111660;
  --color-secondary: var(--color-bg-1);
  --color-white: #ffffff;
  --color-bg-1: #F1F1F1;
  --color-gray: #707070;
  --color-gray-1: rgb(202, 202, 202);
  --box-shadow: 0 3px 6px rgba(0,0,0,.1);

}
