@font-face {
  font-family: 'Gotham-Bold';
  src: url('../font/GothamHTF-Bold.eot');
  src: url('../font/GothamHTF-Bold.eot?#iefix') format('embedded-opentype'),
    url('../font/GothamHTF-Bold.woff') format('woff'),
    url('../font/GothamHTF-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Gotham-Book';
  src: url('../font/GothamHTF-Book.eot');
  src: url('../font/GothamHTF-Book.eot?#iefix') format('embedded-opentype'),
    url('../font/GothamHTF-Book.woff') format('woff'),
    url('../font/GothamHTF-Book.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Gotham-Medium';
  src: url('../font/GothamHTF-Medium.eot');
  src: url('../font/GothamHTF-Medium.eot?#iefix') format('embedded-opentype'),
    url('../font/GothamHTF-Medium.woff') format('woff'),
    url('../font/GothamHTF-Medium.ttf') format('truetype');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

