.faq{
    .title{
        font-size: 3.4rem;
        margin-bottom: 3rem;
        @extend %font-bold;
        color: var(--color-primary);
    }
    h2{
        font-size: 4rem;
        margin-bottom: 2rem;
        color: var(--color-primary);
        @extend %font-bold;
        &:not(:first-child){
            margin-top: 4rem;
        }

        @media(max-width:87.5em){
            font-size: 3.5rem;
        }
        
        @media(max-width:62em){
            font-size: 3rem;
        }
        @media(max-width:48em){
            font-size: 2.4rem;
        }
    }
    h3{
        font-size: 3rem;
        margin-bottom: 2rem;
        color: var(--color-primary);
        @media(max-width:87.5em){
            font-size: 2.7rem;
        }
        @media(max-width:62em){
            font-size: 2.3rem;
        }
        @media(max-width:48em){
            font-size: 2rem;
        }
    }
    h4{
        font-size: 2.4rem;
        margin-bottom: 2rem;
        @media(max-width:87.5em){
            font-size: 2.3rem;
        }
        @media(max-width:62em){
            font-size: 2rem;
        }
        @media(max-width:48em){
            font-size: 1.9rem;
        }
    }
    &__group{
        display: flex;

        &--answer{
            padding-top: 1.5rem;
            padding-bottom: 3.4rem;
            align-self: flex-start;

            .faq__start{
                background-color:var(--color-primary);
                color: var(--color-white);
            }
        }
    }

    &__start{
        flex: 0 0 4rem;
        padding: .5rem;
        justify-content: center;
        align-items: center;
        text-align: center;
        background-color:var(--color-primary);
        color: var(--color-white);
        font-size: 2.4rem;
    }

    &__end{
        text-align: start;
        background-color:transparent;
        color: var(--color-primary);
        padding: 1rem;
        flex: 1 1 100%;
        align-self: stretch;
        font-size: 1.8rem;
        &--answer{
            background-color:transparent;
            @extend %font-normal;
            font-size: 1.6rem;
           
        }
    }
}
.faq__group:nth-child(odd) {
	align-items: flex-start;
	@media (max-width: 767px) { align-items: flex-start; }
}
.faq__group:nth-child(odd) .faq__start {
    background: #F2ECEC;
    color: var(--color-primary);
}