%font-bold{
    font-family: 'Gotham-Bold';
    [dir='rtl'] &{
        font-family: 'Cairo', sans-serif;
        font-weight: 700;
    }
}

%font-normal{
    font-family: 'Gotham-Book';
    [dir='rtl'] &{
        font-family: 'Cairo', sans-serif;
        font-weight: 400;
    }
}

%font-medium{
    font-family: 'Gotham-Medium';
    font-weight: 500;
    [dir='rtl'] &{
        font-family: 'Cairo', sans-serif;
        font-weight: 600;
    }
}

%label{
        padding: 3px 6px;
        background-color: red;
        color: var(--color-white);
        font-family: 'Gotham-Book';
        font-size: 1.3rem;
}


