.form-search {
  margin-bottom: 3rem;
  @media (max-width: 48em) {
    margin-bottom: 0rem;
  }

  &__input {
    border: none;
    outline: none;
    padding: 0;
    padding-inline-start: 4rem;
    padding-inline-end: 2rem;
    height: 3rem;
    width: 100%;
    background-color: transparent;
    border-bottom: 1px solid rgba($color: #000000, $alpha: 0.2);
    @extend %font-normal;
    font-size: 1.8rem;
    background-image: url(../img/icons/search-icon.svg);
    background-size: 2.5rem;
    background-repeat: no-repeat;
    background-position: left bottom;
    transition: all 0.2s ease-in-out;
    &:focus {
      border-bottom: 1px solid var(--color-primary);
    }
    [dir="rtl"] & {
      background-position: right bottom;
    }
  }

  &__label {
    font-size: 1.8rem;
    @extend %font-normal;
    transform: translate(4rem, -6rem);
    transition: all 0.3s;
    [dir="rtl"] & {
      transform: translate(-4rem, -6rem);
    }
  }

  &__input:placeholder-shown + &__label {
    opacity: 0;
    visibility: hidden;
    transform: translate(4rem, -2.9rem);
    [dir="rtl"] & {
      transform: translate(-4rem, -2.9rem);
    }
  }
}

.form-imageUp {
  margin-bottom: 1.5rem;

  &__input {
    background-color: transparent;
    border-radius: 0;
    color: var(--color-primary);
    width: 100%;
    font-size: 1.4rem;
    @extend %font-normal;
  }
}

@keyframes rotateanim {
  0% {
    transform: translate(-1px -1px);
  }

  25% {
    transform: translate(2px -1px);
  }

  50% {
    transform: translate(3px 2px);
  }

  100% {
    transform: translate(2px 0px);
  }
}

.form-log-in {
  display: block;
  &__input {
    width: 100%;
    border: none;
    outline: none;
    height: 4rem;
    margin-top: 1.5rem;
    padding: 0 1.5rem;
    border-radius: 5px;

    &:focus {
      outline: none;
    }
  }

  &__group {
    margin-top: 1rem;
    display: flex;
    align-items: center;
  }

  &__check {
    width: 1.8rem;
    height: 1.8rem;
  }

  &__label {
    font-size: 1.4rem;
    @extend %font-normal;
    padding-inline-start: 0.5rem;
    padding-top: 6px;
    cursor: pointer;
  }

  &__button-group {
    display: flex;
    margin-top: 3rem;

    .btn{
      @extend %font-medium;
    }

    button,
    a:link {
      font-size: 1.6rem;
      padding: 1rem 2rem;
      border-radius: 5px;
      &:hover {
        color: var(--color-primary);
        background-color: var(--color-white);
      }
    }
    button {
      margin-inline-end: 1.5rem;
    }
  }
}

.log-in {
  display: block;
}

.sign-up {
  .log-in-wrap__start{
      position: relative;
    @media(max-width:48.0625em){
      align-items: center;
      padding: 3rem;
    }
    img{
      position: fixed;
      transform: translate(0, -20rem);
      @media(max-width:75em){
        transform: translate(0, -23rem);
      }
      @media(max-width:48.0625em){
        position: static;
        transform: translate(0);
      }

    }
  }
  button {
    background-color: var(--color-white);
    color: var(--color-primary);
  }
}

.documets-add {
  margin-top: 2rem;
  margin-bottom: 3rem;

  &__group {
    display: flex;
    margin-bottom: 1.5rem;
    flex-wrap: wrap;
  }

  &__start {
    flex: 1;
    min-width: 14rem;
  }

  &__end {
    flex: 1;
    min-width: 14rem;
  }
}

.avatar-upload {
  position: relative;
  max-width: 10.5rem;
  cursor: pointer;
}
.avatar-upload .avatar-edit {
  position: absolute;
  right: -9px;
  z-index: 1;
  top: 6px;
}
.avatar-upload .avatar-edit input {
  display: none;
}
.avatar-upload .avatar-edit input + label {
  display: inline-block;
  width: 2.4rem;
  height: 2.4rem;
  margin-bottom: 0;
  border-radius: 100%;
  background: #ffffff;
  border: 1px solid transparent;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.12);
  cursor: pointer;
  font-weight: normal;
  transition: all 0.2s ease-in-out;
}
.avatar-upload .avatar-edit input + label:hover {
  background: #f1f1f1;
  border-color: #d6d6d6;
}
.avatar-upload .avatar-edit input + label:after {
  content: "\f040";
  font-family: "FontAwesome";
  color: #757575;
  position: absolute;
  top: 1px;
  left: 0;
  right: 0;
  text-align: center;
  margin: auto;
}
.avatar-upload .avatar-preview {
  width: 10.5rem;
  height: 10.5rem;
  position: relative;
  border-radius: 5px;
  border: 2px solid #575757;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.1);

  &::before {
    content: "\002B";
    display: inline-block;
    font-size: 5rem;
    font-weight: 400;
    font-family: serif;
    text-align: center;
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-text-stroke: 1px background-color;
  }
}
.avatar-upload .avatar-preview > div {
  width: 100%;
  height: 100%;
  // / border-radius: 100%; /
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  z-index: 2;
}
.avatar-editt, .trader-remove{
  display: inline-block;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  padding: 5px;
  right: -1rem;
  position: absolute;
  font-size: 1.4rem;
  top: 3px;
  line-height: 0.4;
  text-align: center;
  cursor: pointer;
  z-index: 3;
}

html[dir="rtl"] .avatar-editt {
  right: unset;
  left: -1rem;
}
.avatar-remove {
  display: inline-block;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  padding: 5px;
  right: -1.3rem;
  position: absolute;
  background: #ffffff;
  font-size: 3rem;
  top: 3px;
  line-height: 0.4;
  text-align: center;
  cursor: pointer;
  z-index: 3;
  a {
    &:hover {
    }
  }
}

.form-bid {
  &__group {
    button {
      margin-top: 0 !important;
      margin-inline-start: 1.5rem;
      min-width: 15rem;
    }
    display: flex;
    align-items: center;
    &:first-child {
      margin-bottom: 1.5rem;
    }
  }

  &__input {
    height: 4.7rem;
    padding: 5px 1.5rem;
    min-width: 30rem;

    @media (max-width: 57.25em) {
      width: 100%;
      min-width: 10rem;
    }
  }
}

[dir="rtl"] input[type="email"] {
  &:focus {
    text-align: end;
  }
}
