.user-information{
    background-color: var(--color-primary);
    color: var(--white);
    font-size: 1.6rem;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
    grid-auto-rows: auto auto;
    gap: 1.5rem;
    padding: 3rem;
    border-radius: 1.5rem;
    margin-bottom: 3rem;

 

    &__title{
        @extend %font-normal;
        font-size: 1.4rem;
        margin-bottom: 2rem;
        display: block;
        position: relative;
        @extend %font-medium;
        text-transform: uppercase;

        &::after{
            @extend %border-bottom;
            background-color:  var(--color-primary-light);
            bottom: -1rem;
        }
    }

    &__notitle{
        margin-bottom: 5.9rem;
        display: block;

        @media(max-width:30.56em){
            margin-bottom: 0;
        }
    }

    &__info{
        display: block;
        white-space: wrap;
    }
}

.uesr-identity{
    margin-bottom: 0;
    background-color: var(--color-white);
    color: var(--color-primary);
    grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
    box-shadow: var(--box-shadow);

    figure{
        width: 100%;
        height: 40rem;
        overflow: hidden;
        border-radius: 5px;
        margin-bottom: 0 !important;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .trader-remove{
        position: absolute;
        z-index: 99;
        top: 2rem;
        background-color: var(--color-white);
        width: 26px;
        height: 26px;
        padding: 8px;
        cursor: pointer;
        display: block;
        box-shadow: 0 3px 6px rgba($color: #000000, $alpha: .4);

        label{
            cursor: pointer;
        }
    }

    .other-remove-item{
        position: relative;
    }
}