
//notification page

.notification-item{
    padding: 3rem;
    background-color: var(--color-primary);
    color: var(--color-white);
    font-size: 1.6rem;
    border-radius: 1.5rem;
    @media(max-width:48.5em){
        margin-inline-end: 15px;
        margin-inline-start: 15px;
    }
    &:not(:last-child){
        margin-bottom: 3rem;
    }

    &__title{
        font-size: 2rem;
        margin-bottom: 1.5rem;
        display: block;
    
    }

    p, time{
        @extend %font-normal;
        
    }

    time{
        font-size: 1.4rem;
    }

}


//History Page

.section-history{

    &__list{
        display: grid;
        grid-template-columns: repeat(2, minmax(auto, 1fr));
        grid-template-rows: auto auto;
        gap: 3rem;
        @media(max-width:62em){
            grid-template-columns: 1fr;
        }
    }
    &__item{
       background-color: var(--color-white);
       color: var(--color-primary);
       padding: 2rem;
       border-radius: 1.5rem;
       max-width: 100%;
       width: 100%;
       box-shadow: var(--box-shadow);

       label{
           
       }
        h4{
            font-size: 1.8rem;
            @extend %font-medium;
            margin-bottom: 1rem;
        }

        figure{
            overflow: hidden;
            border-radius: 5px;
            height: 13rem;
            flex: 0 1 19rem;

            img{
                width: 100%;
                height: 100%;
                object-fit: cover
                }
        }

        small{
            @extend %font-normal;
        }

        span{
            @extend %font-bold;
            display: block;
            font-size: 2rem;
        }

        .btn{
            margin-top: 2rem;

            &:hover{
                background-color:  var(--color-primary);
                color:var(--color-white);
            }
        }
    }

    &__item-middle{
        display: flex;
        margin-top: 1.5rem;

        &__end{
            margin-inline-start: 1.5rem;
            display: flex;
            flex: 1;
            flex-direction: column;
            align-items: flex-start;
        }
    }
}


//detail page

.item-detail-info{
    padding: 3rem;
    display: grid;
    grid-template-columns: repeat(2, minmax(25rem, 50%));
    background-color: var(--color-white);
    color: var(--color-primary);
    border-radius: 1.5rem;
    box-shadow: var(--box-shadow);
    overflow: hidden;
    column-gap: 3rem;

    @media(max-width:37.5em){
        grid-template-columns: 100%;
        row-gap: 3rem;
        padding: 1.5rem;
    }


    .slick-list {
        line-height: 0;
    }
    &__slider{
        overflow: hidden;
        border-radius: 5px;
        width: 100%;
        height: 47rem;
        @media(max-width:48em){
            height: 25rem;
        }
        figure{
            margin: 0 !important;
            overflow: hidden;
            width: 100%;
            height: 100%;
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }

    &__info{
        @media(max-width:37.5em){
            padding: 0;
        }
    }

    &__bid-next{
        font-size: 1.8rem;
        display: block;
        

        small{
            font-size: 1.4rem;
        }
    }
    button{
        font-size: 1.7rem;
        background-color: var(--color-primary);
        color: var(--color-white);
        padding: 1rem 2rem;
        margin-top: 2rem;
        &:hover{
            color: var(--color-white);
        }
    }
    .deal-item__middle{
        margin: 2rem 0;
    }

    .or{
        font-size: 1.8rem;
        display: block;
        text-transform: uppercase;
        @extend %font-medium;
        margin: 2rem 0;

    }

  

    &__vat{
        opacity: .4;
        font-size: 1.8rem;
        sup{
            font-size: 1.6rem;
            
        }
        margin-top: 2rem;
    }

    &__sub-title{
        display: block;
        position: relative;
        margin-top: 1.5rem;
        &::after{
            @extend %border-bottom;
            bottom: -6px;
        }
    }
    &__amount{
        margin-top: 1.5rem;
        display: block;
        font-size: 2.5rem;
        @extend %font-bold;
        small{
        @extend %font-normal;
        }

    }

    &__title{
        margin-top: 0;
        @extend %font-bold;
        font-size: 2.4rem;
    }
}

.specifications{
    margin: 4rem 0;
    .title{
        font-size: 3.4rem;
        color: var(--color-primary);
        @extend %font-bold;
        position: relative;
        @media(max-width:48em){
            font-size: 2.4rem;
        }

        &::after{
            @extend %border-bottom;
            bottom: -2rem;
        }
    }
}

.specifications-info{
    background-color: var(--color-primary);
    color: var(--color-white);
    padding: 3rem;
    margin-top: 5rem;
    border-radius: 1.5rem;
    @media(max-width:37.5em){
        padding: 1.5rem;
    }
    &__title{
        font-size: 2.3rem;
        position: relative;
        padding-top: 3rem;
        
        
        &::after{
            @extend %border-bottom;
            bottom: -1rem;
            background-color: var(--color-white);
        }
    }
    &__list{
        display: grid;
        grid-template-columns: repeat(4,minmax(20rem, 1fr));
        grid-template-rows: auto auto;
        gap: 1.5rem;
        margin-top: 5rem;
        @media(max-width:51.8em){
            grid-template-columns: repeat(2,minmax(20rem, 1fr));
            margin-top: 3rem;
        }
        @media(max-width:37.5em){
            grid-template-columns: 1fr;
        }
    }

    &__item{
        &__title{
            display: block;
            margin-top: 3rem;
            margin-bottom: 1rem;
            position: relative;
        &::after{
            @extend %border-bottom;
            bottom: -1rem;
            background-color: var(--color-white);
        }
        @media(max-width:37.5em){
           margin-top: 1.5rem;
        }
        }

        &__info{
            @extend %font-normal;
            margin-top: 3rem;
            display: block;
        }
    }
}

.documentation{
    margin: 4rem 0;
    margin-bottom: 0;
    .title{
        font-size: 3.4rem;
        color: var(--color-primary);
        @extend %font-bold;
        position: relative;
        margin-bottom: 6rem;
        overflow-wrap: break-word;
        @media(max-width:48em){
            font-size: 2.4rem;
        }

        &::after{
            @extend %border-bottom;
            bottom: -2rem;
        }
    }

    &__list{
        display: grid;
        grid-template-columns: repeat(4, minmax(20rem, 1fr));
        gap: 1.5rem;
        grid-auto-rows: minmax(18rem, auto);

        @media(max-width:39.5em){
            grid-template-columns: repeat(2, minmax(16rem, 1fr));
        }
    }

    &__item{

    }

    &__list{
        figure{
            margin: 0;
            overflow: hidden;
            border-radius: 5px;
            height: 100%;

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                overflow: hidden;
            }
        }
    }
}

//Privacy Page

.section-privacy{
    h2{
        font-size: 4rem;
        margin-bottom: 2rem;
        color: var(--color-primary);
        @extend %font-bold;
        &:not(:first-child){
            margin-top: 4rem;
        }

        @media(max-width:87.5em){
            font-size: 3.5rem;
        }
        
        @media(max-width:62em){
            font-size: 3rem;
        }
        @media(max-width:48em){
            font-size: 2.4rem;
        }
    }
    h3{
        font-size: 3rem;
        margin-bottom: 2rem;
        color: var(--color-primary);
        @media(max-width:87.5em){
            font-size: 2.7rem;
        }
        @media(max-width:62em){
            font-size: 2.3rem;
        }
        @media(max-width:48em){
            font-size: 2rem;
        }
    }
    h4{
        font-size: 2.4rem;
        margin-bottom: 2rem;
        @media(max-width:87.5em){
            font-size: 2.3rem;
        }
        @media(max-width:62em){
            font-size: 2rem;
        }
        @media(max-width:48em){
            font-size: 1.9rem;
        }
    }
    p, li{
        @extend %font-normal;
        margin-bottom: 1.5rem;
    }
    time{
        display: block;
        margin-bottom: 1rem;
        color: black;
    }
    ul{
        list-style:disc;
        padding-inline-start: 4rem;

        li{
            &::marker{
                font-size: 2rem;
            }
        }
    }
    a{
        text-decoration: none;
        color: var(--color-primary);
    }
}
