// -----------------------------------------------------------------------------
// This file contains all styles related to the header of the site/application.
// -----------------------------------------------------------------------------

.header{
    padding: 2rem 0;
    background-color: var(--color-white);
}
.navigation{
    display: flex;
    align-items: center;
    &__logo{
        width: 11.5rem;
        margin-inline-end: auto;
    }

    &__list{
        list-style: none;
        padding: 0;
        margin: 0;
        margin-inline-end: 11.8rem;
        @media(max-width:48em){
            display: none;
        }
       

       
    }

    &__item{
        display: inline-flex;
        margin-inline-end:4rem;
        position: relative;

        a{
            color: var(--color-gray);
            &:hover,
            &:active
            {
                color: var(--color-primary);
                &::after{
                    @extend %border-bottom;
                    bottom: -1.5rem;
                }
               
            }

            @media(max-width:48em){
                color: var(--color-white !important);

            }
        }
    }

    

}

.user-control{
    @media(max-width:48em){
        display: none;
    }
    span{
        display: inline-flex;
        cursor: pointer;
        align-items: center;
        font-family: 'Cairo', sans-serif;
        font-weight: 700;
        &:hover{
            color: var(--color-primary);
        }
    }

    .fa{
        padding-inline-end: 5px;
    }
    .fa-angle-down{
        font-size: 2.5rem;
    }

    &__log-in{
        cursor: pointer;
        display: inline-flex;
        align-items: center;
        margin-inline-end: 1rem;
        position: relative;

        .user-name{
            color: inherit;
            @extend %font-normal;
            display: inline-block;
            padding-inline-start: 3px;
        }
    }
}

.user-control-menu{
    top:0;
    right: 0;
    transform: translateY(3rem);
    position: absolute;
    height: 0;
    width: 25rem;
    @extend %font-normal;
    background-color: var(--color-primary);
    color: var(--color-white);
    box-shadow: 0 5px 10px rgba($color: #000000, $alpha: .3);
    z-index: 2;
    cursor: auto;
    overflow: hidden;
    opacity: 0;
    transition: all .4s ease-in-out;
    transition-delay: .7s;
    [dir='rtl'] &{
        left: 0;
        right: unset;
    }
    
    ul{
        transform: translateY(-30rem);
        opacity: .2;
        transition: all .2s ease-in-out;
        transition-delay: .3s;
       li{
           &:first-child{
               a{
                   padding-top: 1.5rem;
               }
           }
           &:last-child{
               a{
                   margin-bottom: 1.5rem;
                   text-align: start;
               }
           }
           a{
               width: 100%;
               display: block;
               padding: .8rem 3rem;
               &:hover{
                   background-color: var(--color-primary-light);
               }
           }
       }
        
    }
    &::before{
        content: "";
        width: 100%;
        height: 25rem;
        background-color: var(--color-primary);
        color: var(--color-white);
        background-color: var(--color-white);
        z-index:-1;
        top:0;
        position: absolute;
        transform: translateY(0);
        transition: all .4s ease-in-out;
        transition-delay: .5s;
        @include rtl(left,right,0,0,unset);

    }
    a{
        color: inherit;
        line-height: 3;
    }

}


.menu-active{
    width: 25rem;
    height: 26.5rem;
    opacity: 1;
    transition: all .2s ease-in-out;
    
    ul{
        transform: translateY(0);
        opacity: 1;
        transition: all .6s ease-in-out;
        transition-delay: .3s;
        
       
    }
   
    &::before{
        height: 25rem;
        width: 100%;
        transform: translateY(29rem);
        transition: all .2s ease-in-out;
        transition-delay: .2s;
    }

}


.mobile-menu{
    
    @media(min-width:48em){
        display: none;
    }
    margin-inline-start: 2rem;
    
    .mobile-btn{
        border: none;
        background-color: transparent;

        img{
            width: 3.2rem;
            
        }
    }

    .navigation__list{
        display: flex;
        flex-direction: column;
        position: fixed;
        z-index: 9999;
        height: 100vh;
        width: 100vw;
        transform: translateX(-150rem);
        transition: all .3s ease-in-out;
        transition-delay: .2s;
        left: 0;
        top: 0;
        background-color: var(--color-primary);
        color: var(--color-white);
        padding: 3rem;
        line-height: 3;
        scroll-behavior: smooth;
        [dir='rtl'] &{
            right: 0;
            left: unset;
        }

        &::after{
            display: block;
           position: absolute;
           content: "\2716";
           font-size: 3rem;
           top: 2rem;
           right: 2rem;

           [dir='rtl'] &{
               left: 2rem;
               right: unset;
           }

        }
        
    }

    .mobile-nav-active{
        display: flex;
        flex-direction: column;
        transform: translate(0);
        transition: all .2s ease-in-out;
    
       

        

        .user-mob-name{
            display: flex;
            align-items: center;
            text-align: start;

            .user-name{
                margin-inline-start: 5px;
                font-size: 1.8rem;
            }
        }

        .navigation__item{
            opacity: 1;
            transition: all .3s ease-in-out;
            transition-delay: .003s;
        }
    }

    .navigation__item{
        display: block;
        text-transform: capitalize;
        font-size: 2rem;
        opacity: 0;
        transition: all .3s ease-in-out;
        a{
            color: var(--color-white);
        }
       
    }
    .fa-user{
        font-size: 1.8rem;
        margin: 3rem 0;
    }

    .log-out-mob{
        margin-top: 2rem;
        display: block;
        a{
            color: var(--color-white);
            font-size: 2rem;
            
        }

        span{
            display: block;
            font-size: 2rem;
            font-family: 'Cairo', sans-serif;
        }
    }
}



.stop-scroll {
    margin: 0;
    height: 100%;
    overflow: hidden;
  }


  .active-header
  {
      color: var(--color-primary) !important;

      @media(max-width:48em){
          color: var(--color-white) !important;

      }
      &::after{
          @extend %border-bottom;
          bottom: -1.5rem;
          @media(max-width:48em){
            border-bottom: -1rem;
        }
      }
     
  }

  