.log-in-wrap {
  height: 100vh;
  width: 100vw;
  position: fixed;
  background-image: url(../img/bg-watch.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  
::-webkit-scrollbar {
  width: 8px;
  @media(max-width:48em){
    display: hidden;
    width: 0;
  }
}


::-webkit-scrollbar-track { 
  border-radius: 20px;
  background-color: var(--color-primary);
  @media(max-width:48em){
    border-radius: 0px;
  }
}

::-webkit-scrollbar-thumb {
  background:var(--color-primary-light); 
  border-radius: 5px;
  @media(max-width:48em){
    background:none; 
    border-radius: 0;
  }
}

::-webkit-scrollbar-thumb:hover {
  background: var(--color-primary); 
  @media(max-width:48em){
    background:none;
  }
}

    &__wrap{
        height: 100vh;
        background-image: url(../img/dot-pattern.png);
        width: 100%;
        overflow-y: auto;
        -webkit-overflow-scrolling: touch;
        display: flex;
        align-items: center;
        justify-content: center;
        @media(max-width:48em){
            padding: 1.5rem;
            height: 100vh;
            padding: 10vh 2rem;
            min-height: unset;
        }
    }
  &__log-in {
    max-width: 80rem;
    max-height: 90vh;
    display: grid;
    grid-template-columns: 30rem minmax(30rem, 1fr);
    border-radius: 1.5rem;
    overflow-y: auto;
    box-shadow: 0 6px 18px rgba($color: #000000, $alpha: 1);
   input::placeholder{
        font-size: 1.4rem;
        @extend %font-normal;
    }
    @media (max-width: 48em) {
      grid-template-columns: 1fr;
      max-width: 100%;
      height: 80vh;
    }
  }

  &__start {
    background-color: var(--color-white);
    padding: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      height: 30rem;
      
    @media (max-width: 48em) {
        height: 15rem;
      }
    }
  }

  &__end {
    padding: 3rem;
    background-color: var(--color-primary);
    color: var(--color-white);
    .title {
      font-size: 3rem;
      text-transform: uppercase;
      display: block;
      margin-bottom: 1.5rem;
      position: relative;

      &::after {
        @extend %border-bottom;
        background-color: var(--color-white);
        bottom: -0.5rem;
      }
    }
    p {
      @extend %font-normal;
    }

    a {
      font-size: 1.2rem;
      @extend %font-normal;
    }
  }
}


.alert-success, .alert-danger{
  text-align: center;
  border-radius: 0;
  @extend %font-normal;
  line-height: 1.8;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999;
}

