
%background-iconSize{
    @media(max-width:48em){
        background-size: 3rem !important;
    }
}

.item-nav-tab{
    // margin-top: 2.8rem;
    transition: all .2s;
    transform: translateY(2px);
    @extend %background-iconSize;
    #all{
        @extend %bgimg-icon;
        background-image: url(../img/icons/all.png);
        @extend %background-iconSize;
    }
    #live{
        @extend %bgimg-icon;
        background-image: url(../img/icons/live.png);
        @extend %background-iconSize;
    }
    #corporate{
        @extend %bgimg-icon;
        background-image: url(../img/icons/corporate.png);
        @extend %background-iconSize;
    }
    #inventory{
        @extend %bgimg-icon;
        background-image: url(../img/icons/inventory.png);
        @extend %background-iconSize;
    }
    #deals{
        @extend %bgimg-icon;
        background-image: url(../img/icons/deals.png);
        @extend %background-iconSize;
    }
    &__nav-link{
        flex: 1;
       
    }
    &__nav-link:link,
    &__nav-link:visited{
        transition: all .2s;
        font-size: 1.4rem;
        color: var(--color-white);
        padding: 2rem 1.5rem 2rem 0;
        padding-inline-start: 8rem;
        background-color: var(--color-primary);
        @media (max-width: 50em) { 
            font-size: 1.4rem;

         }
    
        &:before{
            display: block;
            content: attr(title);
            font-weight: bold;
            height: 0;
            overflow: hidden;
            visibility: hidden;
        }
    }

        .nav{

            @media (max-width: 767.98px) { 
                width: 100%;
                
             }

             @media(max-width:45em){
                flex-direction: column;
            }
           
        }

    &__nav-link:active,
    &__nav-link:hover{
        background-color: var(--color-primary-light);
        


    }

   .active{
    color: var(--color-white);
    background-color: var(--color-primary-light);
   }
}