
.deal-item-wrap{
    display: grid;
    grid-template-columns: repeat(3, minmax(27rem, 37.5rem));
    grid-template-rows: auto;
    gap: 2.3rem;
    padding: 5.7rem 0;
    @media(max-width:48em){
        grid-template-columns: 1fr;
    }
}
.deal-item{
    background-color: var(--color-white);
    border-radius: 1.5rem;
    box-shadow: var(--box-shadow);
    padding: 2.5rem 2rem;
    color: var(--color-primary);
    display: flex;
    flex-direction: column;
    label{
        padding: 3px 6px;
        background-color: red;
        color: var(--color-white);
        @extend %font-normal;
        font-size: 1.3rem;
        align-self: flex-start;
        margin-inline-end: 5px;
    }

  

    figure{
        width: 100%;
        height: 21.1rem;
        border-radius: 5px;
        overflow: hidden;
        margin-top: 1rem;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    .title{
        font-size: 2.2rem;
        margin:2.7rem 0 1.5rem 0 ;
        min-height: 6rem;
        @extend %font-bold;

    }

    &__middle{
        display: flex;
        margin-bottom: 2rem;
        @media(max-width:56.25em) and (min-width:48em){
            flex-direction: column;
        }
        span{
            @extend %font-normal;
            text-transform: uppercase;
        }
        &__start{
           flex: 1;
           margin-inline-end: 1.5rem;
        }

        &__end{
            
            flex: 1;
            .amount{
                font-size: 2rem;
                @extend %font-bold;
                @media(max-width:48em){
                    font-size: 2.4rem;
                }
            }

            span+span{
                font-size: 1.3rem;
            }
        }
        .sub-title{
            display: block;
            @extend %font-medium;
        }

        time{
            font-size: 1.4rem;
            font-weight: 600;
            margin: 0 -3px;
            display: block;
            text-transform: lowercase;
            line-height: 2.4;
            @media(max-width:48em){
                font-size: 1.7rem;
            }

            span{
                padding: 0 4px;
            }
        }
    }

    .btn{
        margin-top: auto;
        align-self: flex-start;

        &:hover{
            background-color: var(--color-primary);
            color: var(--color-white);
        }
    }
}

.own-text,
.negotiate-text{
    display: inline-block;
    padding: 3px 6px;
    color: var(--color-white);
    background-color: var(--color-primary-light);
    font-size: 1.3rem;
    @extend %font-normal;
}
.own-text{
    @extend %font-medium;
}