// -----------------------------------------------------------------------------

@mixin rtl($propertyLeft,$propertyRight, $ltr-value, $rtl-value,$valueUnSet) {
    #{$propertyLeft}: $ltr-value;
  
    [dir=rtl] & {
      #{$propertyRight}: $rtl-value;
      #{$propertyLeft}: $valueUnSet;
    }
  }