.form{

    &__group{
        font-family: 'Lato', sans-serif;
    }

    &__group-chec{
        display: inline-block;
    }

    &__input{
        width: 25%;
        padding: .4rem 1rem;
        font-size: 16px;
        font-family: inherit;
        &:not(:last-child){
            margin-right: 1.6rem;
        }

        &:focus{
            outline: 2px solid var(--color-primary-light);
        }

        @include media-breakpoint-down(sm) { 
            display: block;
            width: 100%;
            margin: 1rem 0;
          }
    }

    &__checkbox{
        display: none;
    }

    &__label{
            font-size: 1.4rem;
            font-family: inherit;
            margin-right: 1rem;
    }

    &__checkbox-button{
        height: 1.5rem;
        width: 1.5rem;
        border: 1px solid var(--color-primary);
        display: inline-block;
        position: relative;
        border-radius: 2px;
        margin-right: 1rem;
        top: 3px;

            &::after{
                content: "";
                display: block;
                position: absolute;
                width: 6px;
                height: 6px;
                border-radius: 1px;
                background-color: var(--color-primary);
                left: 50%;
                top:50%;
                transform: translate(-50%, -50%);
                z-index: 10;
                opacity: 0;
            }
    }

    &__checkbox:checked ~ &__label &__checkbox-button::after{
        opacity: 1;
    }

}


